import styled from 'styled-components';

export const Wrapper = styled.div`
  background: transparent;
  width: 100%;
  max-width: 1280px;
  margin: 10px auto;
  padding: 10px;
  width: 90%;

  @media (min-width: 601px) {
    width: 90%;
  }

  @media (min-width: 993px) {
    width: 80%;
  }
`;


